import React, { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import toast from 'react-hot-toast'
import { useDispatch } from 'react-redux'
import Scrollspy from 'react-scrollspy'
import { NavLink } from 'react-router-dom'
import Web3 from 'web3'
import contractAddress from '../../abi/contractAddress'
import RouterContractAddress from '../../abi/routerContractAddress'
import ContractAbi from '../../abi/contractAbi.json'
import RouterContractAbi from '../../abi/routerContractAbi.json'
import { injected } from '../wallet/connectors'
import { setUserWalletAddress,setUserToken } from '../../features/user/userSlice'
import Scramble from 'react-scramble'
import Params from '../../abi/chainParams'
import { auth } from '../../services/auth';

import Modal from "react-modal";

const Header = () => {
  const [click, setClick] = useState(false)
  const handleClick = () => setClick(!click)
  const [navbar, setNavbar] = useState(false)
  const [currentTokenPriceInAvax, setCurrentTokenPriceInAvax] = useState(0)
  const [currentTokenPriceInUSDC, setCurrentTokenPriceInUSDC] = useState(0)
  const { account, activate, deactivate, chainId, library } = useWeb3React()
  const dispatch = useDispatch();
  const ContractAddress = contractAddress
  let contractInstance
  let routerContractInstance
  
  
  
    //OTC MODAL
    const [isOTCOpen, setIsOTCOpen] = useState(false);
    function toggleModalOTC() {
    	setIsOTCOpen(!isOTCOpen);
  	}
  
  const changeBackground = () => {
    if (window.scrollY >= 80) {
      setNavbar(true)
    } else {
      setNavbar(false)
    }
  }

  window.addEventListener('scroll', changeBackground)

  useEffect(async () => {
    if (window.ethereum) {
      if (window.ethereum.enable()) {
        connect()
      }
      //window.ethereum.on('accountsChanged', function (accounts) {
        //window.location.reload(); //DONT USE THIS IT CAUSES CRAZY REFRESH
      //})

      //PRE-LIVE
      if(window.ethereum.chainId !== '0xa869') {
      	//setChainIdToAvax('0xa869'); //FUJI
      }

      //LIVE
      if (window.ethereum.chainId !== '0xa86a') {
        setChainIdToAvax('0xa86a') //MAINNET
      }
    } else {
      toast.error(
        'No metamask or wallet provider installed! If you are on mobile, please use your wallet browser.',
      )
    }
    async function setChainIdToAvax(id) {
      try {
        await window.ethereum.request({
          method: 'wallet_switchEthereumChain',
          params: [{ chainId: id }],
        })
      } catch (switchError) {
        if (switchError.code === 4902) {
          try {
            await window.ethereum.request({
              method: 'wallet_addEthereumChain',
              params: [Params],
            })
            connect()
          } catch (addError) {
            toast.error(
              'Failed to add Avalanche Network to Metamask! Please add it manually.',
            )
          }
        }
        // rejected
        if (switchError.code === 4001) {
        }
      }
    }
  }, [])

  useEffect(async () => {
    if (library && window.ethereum.chainId === '0xa86a') {
	  contractInstance = new library.eth.Contract(ContractAbi, ContractAddress);
      routerContractInstance = new library.eth.Contract(RouterContractAbi, RouterContractAddress);

      const tokenPriceInAvaxWei = await getTokenPriceInAvax(
        '0x9e20Af05AB5FED467dFDd5bb5752F7d5410C832e',
        routerContractInstance,
      )
      const tokenPriceInAvax = Web3.utils.fromWei(
        tokenPriceInAvaxWei.toString(),
        'ether',
      )
      setCurrentTokenPriceInAvax(Number(tokenPriceInAvax))

      let avaxPriceInUsdc = await getAVAXPriceInUSDC(
        '0xb97ef9ef8734c71904d8002f8b6bc66dd9c48a6e',
        routerContractInstance,
      )
      avaxPriceInUsdc = Number(avaxPriceInUsdc / 1000000)
      setCurrentTokenPriceInUSDC(            ( ( (avaxPriceInUsdc * tokenPriceInAvax) / 1)+0.5).toFixed(1))

      contractInstance.events
        .Transfer(
          {
            fromBlock: 'latest',
          },
          function (error, event) {
            //console.log("event");
            //console.log(event);
          },
        )
        .on('data', async function (event) {
          //console.log(event) // same results as the optional callback above

          const tokenPriceInAvaxWei = await getTokenPriceInAvax(
            '0x9e20Af05AB5FED467dFDd5bb5752F7d5410C832e',
            routerContractInstance,
          )
          const tokenPriceInAvax = Web3.utils.fromWei(
            tokenPriceInAvaxWei.toString(),
            'ether',
          )
          setCurrentTokenPriceInAvax(Number(tokenPriceInAvax))
    
          let avaxPriceInUsdc = await getAVAXPriceInUSDC(
            '0xb97ef9ef8734c71904d8002f8b6bc66dd9c48a6e',
            routerContractInstance,
          )
          avaxPriceInUsdc = Number(avaxPriceInUsdc / 1000000)
          setCurrentTokenPriceInUSDC(            ( ( (avaxPriceInUsdc * tokenPriceInAvax) / 1)+0.5).toFixed(1))
        })
        .on('changed', (event) => {
          //remove event from local database
          console.log(event) // same results as the optional callback above
        })
        .on('error', (error) => {
          console.log(error) // same results as the optional callback above
        })
    }
  }, [library, currentTokenPriceInAvax, currentTokenPriceInUSDC])
  useEffect(() => {
    if (account !== undefined) {
      getCredentials();
    }
  },
  [account]);
  const getCredentials = async () => {
    const res = await auth(account);
    dispatch(setUserToken(res.token));
    dispatch(setUserWalletAddress(account));
  };
  async function connect() {
    try {
      await activate(injected, undefined, true)
      //console.log(account);
    } catch (ex) {
      toast.error('Unable to connect! Are you on the right network?')
    }
  }

  // use example: const price = await getTokenPrice("0x40064CE057Fb99a5c8e34F61365cC5996E59aB57");
  async function getTokenPriceInAvax(tokenAddress, routerInstance) {
    const path = await getPathTokenForAvax(tokenAddress)

    let price = await routerInstance.methods
      .getAmountsOut('1000000000000000000', path)
      .call()
      .then((result) => {
        return result
      })
      .catch((e) => {
        console.log({ e })
        return 0
      })
    return price[1]
  }

  async function getAVAXPriceInUSDC(tokenAddress, routerInstance) {
    const path = await getPathAvaxForTokens(tokenAddress)

    let price = await routerInstance.methods
      .getAmountsOut('1000000000000000000', path)
      .call()
      .then((result) => {
        return result
      })
      .catch((e) => {
        console.log({ e })
        return 0
      })
    return price[1]
  }

  async function getPathAvaxForTokens(tokenAddress) {
    return ['0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7', tokenAddress]
  }

  async function getPathTokenForAvax(tokenAddress) {
    return [tokenAddress, '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7']
  }

  async function disconnect() {
    try {
      deactivate()
    } catch (ex) {
      toast.error('Unable to disconnect!')
    }
  }

  return (
    <>
      <div className="shane_tm_topbar">
        <div className={navbar ? 'topbar_inner opened' : 'topbar_inner'}>
          <div className="logo">
            <NavLink to="/">
              <img
                src="/img/logo/projectx-logo-3-letters-onblack.png"
                alt="projectx"
                height="60"
              />
            </NavLink>
          </div>
          {/* End logo */}
          <div className="menu">
            <Scrollspy
              className="anchor_nav"
              items={["price","home","nodes","mission","buy","otc","connect"]}
              currentClassName="current"
              offset={-200}
            >
              <li>
                {account && (
	               <a href="https://coinmarketcap.com/currencies/project-x-nodes/" target="_blank" rel="noreferrer">
				   	<Scramble autoStart text={"$"+currentTokenPriceInUSDC.toString()} steps={[{roll: 5, action: "+", speed: "slow", type: "all"},{action: "-",speed: "slow",type: "random"}]}/>
				   </a>
				)}
              </li>
              <li className="hide">
                <a href="#home"></a>
              </li>
              <li>
                <a href="#nodes">
                  <Scramble
                    autoStart
                    text={'Nodes'}
                    steps={[
                      { roll: 10, action: '+', speed: 'slow', type: 'all' },
                      { action: '-', speed: 'slow', type: 'random' },
                    ]}
                  />
                </a>
              </li>
              <li>
                <a href="#mission">
                  <Scramble
                    autoStart
                    text={'Vision'}
                    steps={[
                      { roll: 15, action: '+', speed: 'slow', type: 'all' },
                      { action: '-', speed: 'slow', type: 'random' },
                    ]}
                  />
                </a>
              </li>
              <li>
                <a
                  href="https://traderjoexyz.com/trade?outputCurrency=0x9e20af05ab5fed467dfdd5bb5752f7d5410c832e"
                  className="buy"
                  target="_blank"
                  rel="noreferrer"
                >
                  <Scramble
                    autoStart
                    text={'Buy $PXT2'}
                    steps={[
                      { roll: 20, action: '+', speed: 'slow', type: 'all' },
                      { action: '-', speed: 'slow', type: 'random' },
                    ]}
                  />
                </a>
              </li>
              <li>
                <a
                  href="#"
                  className="buy"
                  rel="noreferrer"
                  onClick={toggleModalOTC}
                >
                  <Scramble
                    autoStart
                    text={'OTC'}
                    steps={[
                      { roll: 15, action: '+', speed: 'slow', type: 'all' },
                      { action: '-', speed: 'slow', type: 'random' },
                    ]}
                  />
                </a>
              </li>
              <li>
                <a
                  href="https://twitter.com/ProjectXNodes"
                  target="_blank"
                  rel="noreferrer"
                >
                  <img
                    className="svg svg_white"
                    src={`/img/svg/social/twitter.svg`}
                    alt="twitter"
                  ></img>
                </a>
              </li>
              <li>
                <a
                  href="https://discord.com/invite/projectx"
                  target="_blank"
                  rel="noreferrer"
                >
                  <img
                    className="svg"
                    src={`/img/svg/social/discord.svg`}
                    alt="discord"
                  ></img>
                </a>
              </li>

              <li>
                {account ? (
                  <button className="btn" onClick={disconnect}>
                    {'0x...' + account.slice(37)}
                  </button>
                ) : (
                  <button className="btn" onClick={connect}>
                    <Scramble
                      autoStart
                      text={'Connect'}
                      steps={[
                        { roll: 20, action: '+', type: 'all' },
                        { action: '-', speed: 'slow', type: 'random' },
                      ]}
                    />
                  </button>
                )}
              </li>
            </Scrollspy>
          </div>
          {/* End menu */}
        </div>
      </div>
      {/* End shane_tm_topbar */}

      {/* Start shane mobile menu */}
      <div className="shane_tm_mobile_menu">
        <div className="topbar_inner">
          <div className="container bigger">
            <div className="topbar_in">
              <div className="logo">
                <NavLink to="/">
                  <img
                    src="/img/logo/projectx-logo-3-letters-onwhite.png"
                    alt="projectx"
                    height="40"
                  />
                </NavLink>
              </div>
              {/* End logo */}
              <div className="my_trigger" onClick={handleClick}>
                <div
                  className={
                    click
                      ? 'hamburger hamburger--collapse-r is-active'
                      : 'hamburger'
                  }
                >
                  <div className="hamburger-box">
                    <div className="hamburger-inner"></div>
                  </div>
                </div>
                {/* End hamburger menu */}
              </div>
            </div>
          </div>
        </div>
        <div className={click ? 'dropdown active' : 'dropdown'}>
          <div className="container">
            <span className="close_menu" onClick={handleClick}>
              close
            </span>
            <div className="dropdown_inner">
              <ul className="anchor_nav">
              <li>
                {account && (
	               <a target="_blank" href="https://coinmarketcap.com/currencies/project-x-nodes/" rel="noreferrer">
				   	{"$"+currentTokenPriceInUSDC.toString(2)}
				   </a>
				)}
              </li>
                <li className="current">
                  <a href="#home" onClick={handleClick}>
                    Home
                  </a>
                </li>
                <li>
                  <a href="#nodes" onClick={handleClick}>
                    Nodes
                  </a>
                </li>
                <li>
                  <a href="#mission" onClick={handleClick}>
                    Vision
                  </a>
                </li>
                <li>
                  <a
                    href="https://traderjoexyz.com/trade?outputCurrency=0x9e20af05ab5fed467dfdd5bb5752f7d5410c832e"
                    className="buy"
                    target="_blank"
                    rel="noreferrer"
                  >
                    Buy $PXT2
                  </a>
                </li>
                <li>
                  <a
                    href="https://forms.gle/hf7QfxytJYRzYrn58"
                    className="otc"
                    target="_blank"
                    rel="noreferrer"
                  >
                    OTC
                  </a>
                </li>
                <li>
                  <a
                    href="https://twitter.com/ProjectXNodes"
                    target="_blank"
                    rel="noreferrer"
                  >
                    <img
                      className="svg svg_white"
                      src={`/img/svg/social/twitter.svg`}
                      alt="twitter"
                    ></img>
                  </a>
                </li>
                <li>
                  <a
                    href="https://discord.com/invite/projectx"
                    target="_blank"
                    rel="noreferrer"
                  >
                    <img
                      className="svg"
                      src={`/img/svg/social/discord.svg`}
                      alt="discord"
                    ></img>
                  </a>
                </li>

                <li>
                  {account ? (
                    <button className="btn" onClick={disconnect}>
                      0x...{account.slice(37)}
                    </button>
                  ) : (
                    <button className="btn" onClick={connect}>
                      Connect
                    </button>
                  )}
                </li>

                {/*
                <li>
                	<br/>
                    <button
                      className="btn"
                    >
                      Connect soon
                    </button>
                </li>
                */}
              </ul>

              {/* End social share */}
            </div>
          </div>
          {/* End container */}
        </div>
      </div>
      {/* End shane mobile menu */}
      
      
      
      <Modal
        isOpen={isOTCOpen}
        onRequestClose={toggleModalOTC}
        contentLabel="OTC"
        className="custom-modal"
        overlayClassName="custom-overlay"
        closeTimeoutMS={500}
      >
        <div className="shane_tm_modalbox_news">
          <button className="close-modal" onClick={toggleModalOTC}>
            <img src="/img/svg/cancel.svg" alt="close icon" />
          </button>
          {/* End close modal */}
          <div className="box_inner">
            <div className="description_wrap scrollable">
              <div className="details">
                <h3 className="title">
                  Over the Counter Requests
                </h3>
              </div>
              {/* End details */}
              <div className="description">
                    
                    <p>Our OTC service is managed in our <a href="https://discord.gg/a9bwpTCP"  target="_blank">Discord channel.</a><br/>
					Please file a <a href="https://discord.gg/a9bwpTCP"  target="_blank">service desk ticket with Discord</a> to submit your request.</p>
					<h5>How do OTC trades work?</h5>
					<p>There are 2 Methods for OTC:</p>
					<h5>Method 1: OTC Swap</h5>
					<p>1. We will pair you with another individual who also wants to make a buy or sell order at approximately the same $ amount.</p> 
					<p>2. To facilitate the OTC, we will be using the team wallet (verify address before sending) which is currently owned by the Team. The wallet and transactions will be shared and publicly available to both parties.</p>
					<p>3. The Buyer and seller will send agreed upon payment method and amount to the team wallet (verify address before sending) and a team member will distribute the respective funds to each party.</p>
					<p>Note: We are not charging any fees for facilitating this transaction</p>
					<h5>Method 2: Team Swap</h5>
					<p>We (X Oracle) will assist with buying or selling from our Team Wallets.</p>
              </div>
              {/* End description */}
            </div>
          </div>
        </div>
      </Modal>
    </>
    
    
  )
}

export default Header
