import React, {useEffect, useState,useContext} from "react";
import ReactDOM from 'react-dom';
import ContractAbi from "../../abi/contractAbi.json";
import contractAddress from "../../abi/contractAddress";
import nodeRewardContract from "../../abi/nodeRewardContract";
import {useWeb3React} from "@web3-react/core";
import toast from "react-hot-toast";
import NODERewardManagementAbi from "../../abi/NODERewardManagement.json";
import Web3 from "web3";
import axios from 'axios';
import { ReactReduxContext } from 'react-redux'
import { getNodeList } from '../../services/list';
import { getNodeStatus } from '../../services/node_status';
// import NodeList from '../../assets/nodes.json';
import NameList from '../../assets/nebulas_shuffled.json';

//RPC
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css'; // optional
import {CopyToClipboard} from 'react-copy-to-clipboard';
import Modal from "react-modal";


let hasRunNodeInfo = false;
//const dateObject = new Date();

const AboutTwo = () => {
    const {account, library} = useWeb3React();
    const [isLoading, setIsLoading] = useState(false);
    const [pendingRewards, setPendingRewards] = useState(0);
    const [claimableNumberOfNodes, setClaimableNumberOfNodes] = useState(0);
    const [myNodes, setMyNodes] = useState(0);
    const [totalPending, setTotalPending] = useState(0);
    const [canClaimAll, setCanClaimAllAll] = useState(false);
    const [rows, setRows] = useState([]);
    const { store } = useContext(ReactReduxContext)
    let NODERewardManagementInstance;
    let contractInstance;
    const dateObject = new Date();
    
    //RPC MODAL
    const [isRPCOpen, setIsRPCOpen] = useState(false);
    function toggleModalRPC() {
    	setIsRPCOpen(!isRPCOpen);
  	}
    
    //SETTING
    const dailyReward = 0.17;

    useEffect(async () => {
        if (library) {
            contractInstance = new library.eth.Contract(ContractAbi, contractAddress);
            NODERewardManagementInstance = new library.eth.Contract(
                NODERewardManagementAbi,
                nodeRewardContract
            );
            await getNodesInfo();
        }
    }, [
        library,
        isLoading,
        pendingRewards,
        myNodes,
        canClaimAll,
        rows,
        claimableNumberOfNodes,
        totalPending
    ]);

    // const getNodeListApi = async () => {
    //     const nodes = await getNodeList();
    //     setNodesList(getNodeList());
    //     console.log(nodesList);
    // }

    /*const claimFromSingleNode = async (nodeIndex) => {
        return await contractInstance.methods
            .cashoutReward(nodeIndex)
            .send({from: account})
            .then((result) => {
                // todo refresh all available button
                setClaimableNumberOfNodes(claimableNumberOfNodes - 1)
                return result;
            })
            .catch((e) => {
                console.log({e});
                if (pendingRewards > 0) toast.error("Unable to get your reward data.");
                return e;
            });
    }*/

    const getNodesInfo = async () => {
    	let isOwner = await NODERewardManagementInstance.methods
            ._isNodeOwner(account)
            .call()
            .then((res) => {
                return res
            })
            .catch((e) => {
                console.log({e});
                return false;
            });
        if (!isOwner) {
        	return false;
    	}
    			
	    if (!hasRunNodeInfo) {
			hasRunNodeInfo = true;
			let RunNodeInfoAt = dateObject.getMilliseconds();
			
	        const numberOfNodes = await NODERewardManagementInstance.methods
                ._getNodeNumberOf(account)
	            .call()
	            .then(async (myNodes) => {
	                return myNodes;
	            }).catch((e) => {
	                return -1;
	                console.log({e});
	                toast.error(
	                    "Unable to get your node data, Are you on the right wallet & network?"
	                );
	            });
	            
	
	        if (numberOfNodes > 0) {
	            setMyNodes(numberOfNodes);
	            let totalClaimableNodes = 0;
				var myClaimableSum = 0;
				const nodeElement = [];
	
	            //Pending Rewards (unclaimable yet) //NOT NEEDED
		        /*const rewardsPendingAllNodes = await NODERewardManagementInstance.methods
	                ._getNodesPendingClaimableAmount(account)
	                .call()
	                .then((amount) => {
	                    return amount
	                })
	                .catch((e) => {
	                    console.log({e});
	                    //console.log("too early to claim");
	                });
	            const myPendingArray = rewardsPendingAllNodes.split("#");
	            var myPendingSum = myPendingArray.reduce(function(a, b){
					return Number(a) + Number(b);
	    		}, 0);
	            console.log('Pending rewards (not claimable yet):'+myPendingSum);
	            */
	            
	            //RPC CLICK
	            function clickRPC(e) {
					e.preventDefault();
                    //navigator.clipboard.writeText(NodesApi[0].rpc_url);
					toast.success(
	                    "RPC launching soon, watch this space!"
	                );
  				}

	            //GET CLAIMABLE
	            const rewardsAllNodes = await NODERewardManagementInstance.methods
	                ._getNodesRewardAvailable(account)
	                .call()
	                .catch((e) => {
	                    console.log({e});
	                });
                const token = store.getState().user.userToken;
                const NodesApi = await getNodeList(token);
                //console.log(NodesApi);
	            if (rewardsAllNodes) {
		            const myClaimableArray = rewardsAllNodes.split("#");
		            for (let i = 0; i < myClaimableArray.length; i++) {
			            let node_status = false;
			            let rpcURL = "RPC URL coming soon for this node";
			            if (NodesApi[i]) {
				            node_status = true; //await getNodeStatus(NodesApi[i].rpc_url);
			            	rpcURL = "https://avax-rpc.projectx.financial/"+NodesApi[i].rpc_url;
			            }
			            
		                myClaimableSum += Number(myClaimableArray[i]);
		                const claimablePXTforNode = Web3.utils.fromWei(myClaimableArray[i].toString(), 'ether');
                        const rand = Math.round(Math.random() * (3 - 0) + 0);
		                if (Number(myClaimableArray[i]) > 0.0) {
			                if (claimablePXTforNode >= 0) {
		                    	totalClaimableNodes ++;
		                    }
		                    
		                    nodeElement.push(
		                        <tr key={i}>
		                            <td>{NameList[i]}</td>
		                            <td>{Number(claimablePXTforNode).toFixed(3)} $PXT2</td>
		                            {/*<span className="indent noderow">{((100 * ((hours) * rPM)) / pxtPerNode).toFixed(1)}% ROI</span>*/}
		                            <td><Tippy allowHTML={true} content={rpcURL} theme={"blue"} interactive={true}><span className="secondarycolour_text_blue pointer" onClick={toggleModalRPC}>RPC <i className="fa fa-info-circle"></i> {node_status ? <i className="fa fa-check-circle"></i> : <i className="fa fa-times-circle"></i>}</span></Tippy></td>
		                            {/*<td>Frankfurt</td>*/}
		                            <td>READY</td>
		                            {/*<button className="btn nodeclaim" onClick={async (e) => {await claimFromSingleNode(i)}}>CLAIM</button>*/}
		                        </tr>
		                    );
		                } else {
		                    nodeElement.push(
		                        <tr key={i}>
		                            <td>{NameList[i]}</td>
		                            <td>{Number(claimablePXTforNode).toFixed(3)} $PXT</td>
		                            {/*<span className="indent noderow">{((100 * ((hours) * rPM)) / pxtPerNode).toFixed(1)}% ROI</span>*/}
		                            <td><Tippy allowHTML={true} content={rpcURL} theme={"blue"} interactive={true}><span className="secondarycolour_text_blue pointer" onClick={toggleModalRPC}>RPC <i className="fa fa-info-circle"></i> {node_status ? <i className="fa fa-check-circle"></i> : <i className="fa fa-times-circle"></i>}</span></Tippy></td>
		                            {/*<td>Frankfurt</td>*/}
		                            <td>WAIT 1HR</td>
		                            {/*<button className="btn nodeclaim" disabled>ENERGISING</button>*/}
		                        </tr>
		                    );
		                }
		            }
		        }
	
	            //BUILD TABLE
	            setRows(nodeElement);
	
	            //DISPLAY REWARDS TOTAL
	            const r = Web3.utils.fromWei(myClaimableSum.toString(), 'ether');
	            setPendingRewards(Number(r));
	
	            //ENABLE CLAIM BUTTON?
	            setClaimableNumberOfNodes(totalClaimableNodes);
	            //console.log(totalClaimableNodes + ' '+ numberOfNodes);
	            //if (Number(totalClaimableNodes) === Number(numberOfNodes)) {
	            //if (Number(totalClaimableNodes) > 0 && r >= 0.17) {
		        if (Number(totalClaimableNodes) > 0) {
	                setCanClaimAllAll(true);
	            }
	
	            //DEBUG
	            console.log('Claimable Nodes:'+totalClaimableNodes+' and Claimable Rewards:'+myClaimableSum);
	            //console.log('Number of node:'+numberOfNodes);
	        }
        }
    };


    const getNodeCreationTime = async () => {
        const res = await NODERewardManagementInstance.methods
            ._getNodesCreationTime(account)
            .call()
            .then((creationTime) => {
                return creationTime.split('#');
            })
            .catch((e) => {
                console.log({e});
                if (pendingRewards > 0) toast.error("Unable to get your reward data.");
            });
        return res;
    };


    const getRewardsPerMinute = async () => {
        const res = await NODERewardManagementInstance.methods
            .rewardsPerMinute()
            .call()
            .then((creationTime) => {
                return Web3.utils.fromWei(creationTime);
            })
            .catch((e) => {
                console.log({e});
                if (pendingRewards > 0) toast.error("Unable to get your reward data.");
            });
        return res;
    };


    const OnClickClaimButton = async () => {

        /*
        const node = '{"name":"YOUR_NAME","description":"YOUR_DESCRIPTION"}'
        const response = await axios.post('https://api.chainstack.com/v1', node, {
            headers: {
            'HDR_CT': 'application/json',
            'HDR_AUTH': 'Authorization: Bearer gozjNHMX.JV6ZFDLbKHnb0b8KK9jHFlriSBx70uMz'
            }}).then((response) => {
                return response.data.data.normal.price;
            });

        console.log(response)
        */

        const gasPrice = await axios.get('https://api.debank.com/chain/gas_price_dict_v2?chain=avax')
            .then((response) => {
	            //console.log("gas: " + response.data.data.fast.price + " " + parseInt(response.data.data.fast.price - ((response.data.data.fast.price/100)*1)));
                return parseInt(response.data.data.fast.price);
            });

        setIsLoading(true);
        contractInstance.methods
            .cashoutAll()
            .send({from: account, gasPrice: gasPrice})
            .then((claimResult) => {
                toast.success("Congratulations! $PXT claimed successfully.");
                setPendingRewards(0);
                setIsLoading(false);
            })
            .catch((e) => {
                console.log({e})
                /*
                toast.error(
                  "Unable to claim your rewards, Are you on the right wallet & network?"
                );
                */
                setIsLoading(false);
            });
    };


    return (
        <>
            <div className="shane_tm_section" id="nodes">
                <div className="shane_tm_about bg_black">
                    <div className="container">
                        <div className="about_inner">
                            <div className="left">
                                {myNodes > 0 && <h5>{myNodes} nodes activated:</h5>}
                                {myNodes > 0 ? (
                                    //<div id="nodelist"> {rows} </div>
                                    <div id="nodelist">
	                                    <table id="nodelist_table">
											<tbody>
                                                {rows}
                                            </tbody>
										</table>
									</div>
                                ) : (
                                    <span className="indent">
										No nodes initialized. <a href="#home">Create some now</a>
										<p>Maybe your wallet is not connected?</p>
									</span>
                                )}
                                
                               <br/>
                               You can track rewards for multiple wallets <a href="https://my.projectx.financial/" target="_blank" rel="noreferrer">here</a>
                            </div>
                            
                            {/* End left */}

                            <div className="right">
                                <div className="shane_tm_title" data-aos="fade-up" data-aos-duration="1200">
                                    <h3>{pendingRewards.toFixed(2)} $PXT2<span>claimable reward</span></h3>
                                </div>
                                <div className="text" data-aos="fade-up" data-aos-duration="1200">
                                    <p>Daily rewards: <span className="secondarycolour_text">{dailyReward} $PXT2</span></p>
                                    <p>Reward interval: <span className="secondarycolour_text">1hr</span></p>
                                    <p>Node cost: <span className="secondarycolour_text">10 $PXT2</span></p>
                                    <p>Claim tax: <span className="secondarycolour_text">0%</span></p>
                                    <p>Sell tax: <span className="secondarycolour_text">18%</span></p>
                                    <p>Ensure you are at: <a href="https://my.projectx.financial"  rel="noreferrer">https://projectx.financial</a></p>
									<br/>
									
                                    {canClaimAll ? (
                                        <button className="btn" onClick={OnClickClaimButton}>CLAIM ALL NODES</button>
                                    ) : (
                                        <button className="btn" disabled onClick={OnClickClaimButton}>Please wait {claimableNumberOfNodes} of {myNodes} ready</button>
                                    )}
                                 
                                    {myNodes > 0 && <span><br/><br/><p><i class="fa fa-exclamation-triangle"></i> There are intermittent claim and node create issues.<br/>If you get high gas (above 0.002 AVAX per node), please reject and try later. The issue is being investigated.</p></span>}
                                </div>
                                
                                {/*<div data-aos="fade-up" data-aos-duration="1200">
                                	Claim disabled until V2 migration complete<br/>See <a href="https://discord.com/invite/projectx" rel="noreferrer">Discord</a> for further details
                                </div>*/}
                                
                            </div>
                            {/* End right */}
                        </div>
                    </div>
                    {/* End container */}
                </div>
            </div>
            
            <Modal
	            isOpen={isRPCOpen}
	            onRequestClose={toggleModalRPC}
	            contentLabel="RPC"
	            className="custom-modal"
	            overlayClassName="custom-overlay"
	            closeTimeoutMS={500}
	          >
	            <div className="shane_tm_modalbox_news">
	              <button className="close-modal" onClick={toggleModalRPC}>
	                <img src="/img/svg/cancel.svg" alt="close icon" />
	              </button>
	              {/* End close modal */}
	              <div className="box_inner">
	                <div className="description_wrap scrollable">
	                  <img src="/img/news/rpc-avax.png" alt="thumb" width="500"/><br/>
	                  <div className="details">
	                    <h3 className="title">
	                      ProjectX RPC
	                    </h3>
	                  </div>
	                  {/* End details */}
	                  <div className="description">
	                  		<p>RPC (Remote Procedure Call) Endpoints are now live for each and every node in the Project X system, deployed to over 79,000 nodes. This is a HUGE achievement in our roadmap!</p>
							<h5>What is RPC?</h5>
							<p>Every node needs a way to communicate with the blockchain. This connection between the node and blockchain is called RPC. To set this up we need our own routing system that can be linked to these RPCs, and each node has its unique URL.</p>
							<h5>Why RPC?</h5>
							<p>Until this point, communication was going through AVAX/MetaMask's public routing. Now each of the nodes in Project X has a private connection using its unique RPC URL (accessed through the Project X website). This should ideally help in potentially reducing transaction fees and provide a faster transaction time and better wallet performance.</p>
							<p>With a successful rollout of RPC Endpoints across all the nodes currently in the Project X system, we can say Project X has officially expanded its footprint into Nodes-as-a-Service (NaaS).</p>
							<h5>How do I use them?</h5>
							<p></p>
							<p>1. Copy the endpoint <strong>URL</strong> by hovering over the blue RPC link on your node.</p>
							<p>2. Go into your MetaMask <strong>SETTINGS {'>'} NETWORKS {'>'} AVALANCHE MAINNET</strong>, and <strong>edit</strong> the URL as the RPC endpoint. If you already have an existing URL set up, <strong>replace</strong> it with the <strong>URL</strong> you copied.</p>							
							<p>Thank you everyone for your constant support and keep our community growing!</p>
							<p>We're just getting started! x</p>
							{/*<h5>How to set up RPC?</h5>*/}
	                  </div>
	                  {/* End description */}
	                </div>
	              </div>
	            </div>
	          </Modal>
        </>
    );
};

export default AboutTwo;
